export default {
  name: 'RenderCell',
  functional: true,
  props: {
    render: Function,
    backValue: String
  },
  render: (h, ctx) => {
    console.log(ctx.parent, 'render dom')
    // parent   divparent parent
    return ctx.props.render(h, ctx.props.backValue, ctx.parent)
  }
}



