const Mock = require('mockjs')
const utils = require('./utils')

module.exports = function (app) {
// http
  app.get('/api/tableList', function (rep, res) {
    // mock datajson
    // util.getJsonFilejson
    var json = utils.getJsonFile('./tableList.json')
    // json Mock.mock 
    res.json(Mock.mock(json))
  })
}



