import Vue from 'vue'
import Extend from './Extend.vue'
let MyAlertConstructor = Vue.extend(Extend)
let instance
var MyAlert = function (message) { // 
  // 
  instance = new MyAlertConstructor({
    data: {
      message: message
    }})
  // 
  instance.$mount()
  document.body.appendChild(instance.$el)
  return instance
}
export default MyAlert



