const fs = require('fs')// 
const path = require('path')// path

module.exports = {
  // json
  getJsonFile: function (filePath) {
      // json
    var json = fs.readFileSync(path.resolve(__dirname, filePath), 'utf-8')
      // 
    return JSON.parse(json)
  }
}



