<template>
  <div class="provide">
    provide
    <provide-child ref='provideChild'></provide-child>
    <el-button @click="handleAdd"></el-button>
    <el-button type="primary" @click="handleProvide"></el-button>
  </div>
</template>
<script>
import Vue from 'vue'
import ProvideChild from './ProvideChild'
export default {
  name: 'provide',
  data () {
    return {
      datas: [
        {
          id: 1,
          label: ''
        },
        {
          id: 1,
          label: ''
        },
        {
          id: 1,
          label: ''
        }
      ],
      number: 10,
      message: 100000
    }
  },

  components: {
    ProvideChild
  },

  // 
  // 
  //      

  provide () { //  
    return {
      message: this.message,
      datas: this.datas,
      _this: this // this  
    }
  },

  // :2.6API Vue.observable  provide
  // provide () {
  //   this.message = Vue.observable({
  //     color: 'blue'
  //   })
  //   return {
  //     theme: this.theme
  //   }
  // },

  mounted () {
    this.$refs.provideChild.handleChange() // 
  },

  methods: {
    handleClick (value) {
      console.log('click ' + value)
    },

    handleAdd () {
      this.number += 1
      console.log(this.number)
    },

    handleProvide () {
      this.message += 1
    }
  }
}
</script>



